import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-visitor-details',
  templateUrl: './visitor-details.component.html',
  styleUrls: ['./visitor-details.component.scss'],
  animations: [routerTransition()]

})
export class VisitorDetailsComponent implements OnInit {
  societyadmin = JSON.parse(localStorage['societyadmin']);
  visitor = JSON.parse(localStorage['visitor']);


  visitor_details: any;
  visitor_name: string = this.visitor.visitor_name;
  visitor_email: string = this.visitor.email;
  visitor_contact: string = this.visitor.contact;
  visitor_date:any = this.visitor.Created_on;
  visitor_in_time: string = this.visitor.In_time;
  visitor_out_time: string = this.visitor.Out_time;
  visitor_vehicle_type: string;
  visitor_vehicle_no: string;

  visitor_profile_image: string;
  visitor_document_image: string;

  flatlist:any;
  constructor(private http: HttpClient) { 
  }

  ngOnInit() {
    this.getFlatownerData();

  }

  getFlatownerData(){
    this.http.get('api/visitor/' + this.visitor._id).subscribe(data=> {


      this.visitor_details = data[0];

      console.log(this.visitor_details);

      this.visitor_name= this.visitor_details.visitor_name;
      this.visitor_email= this.visitor_details.email;
      this.visitor_contact= this.visitor_details.contact;
      this.visitor_date = this.visitor_details.Created_on;
      this.visitor_in_time = this.visitor_details.In_time;
      this.visitor_out_time = this.visitor_details.Out_time;
      this.visitor_profile_image = this.visitor_details.profile_path;
      this.visitor_document_image = this.visitor_details.document_path;
      this.visitor_vehicle_no = this.visitor_details.vehicle_no;
      this.visitor_vehicle_type = this.visitor_details.vehicle_type
      this.flatlist = this.visitor_details.Flat_id

      localStorage.setItem('flatowner', JSON.stringify(this.visitor_details));

   
     });
  
  }

}



